}

.products__btn--line:after {
  bottom: 3px;
  transform: rotate(45deg);
}

.products__btn:hover .products__btn--line {
  width: 50px;
}

.products__btn:hover .products__btn--line:before, .products__btn:hover .products__btn--line:after {
  left: 43px;
}

.products__btn:focus .products__btn--line {
  width: 50px;
  height: 3px;
}

